import React, { Component } from 'react'

export default class Pages extends Component {
    constructor(){
        super();
        this.state = {
            pageCurr:1,
            totalPage:10,
            pages:[]
        }
    }
    //初始分页栏
    componentDidMount(){
        // console.log(this.state);
        var pages = [];
        pages.push(<li onClick={this.pageLeft} key={0}>《</li>)
        for(let i=1;i<=this.state.totalPage;i++){
            if(i === this.state.pageCurr){
                pages.push(<a><li onClick={this.pageChange} onClick={this.props.handlePage} className="active" key={i} value={i}>{i}</li></a>)
            }else{
                pages.push(<a><li onClick={this.pageChange} onClick={this.props.handlePage} key={i} value={i}>{i}</li></a>)
            }  
        }
        pages.push(<li onClick={this.pageRight} key={this.state.totalPage+1}>》</li>)
        this.setState({
            pages:pages
        })
    }
    //出现点击之后更改选中
    page(number){
        var pages = this.state.pages;
        for(let i=1;i<=this.state.totalPage;i++){
            if(i === number){
                pages[i] = <a><li onClick={this.pageChange} onClick={this.props.handlePage} className="active" key={i} value={i}>{i}</li></a>
            }else{
                pages[i] = <a><li onClick={this.pageChange} onClick={this.props.handlePage} key={i} value={i}>{i}</li></a>
            }  
        }
        this.setState({
            pages:pages,
            pageCurr:number
        });
    }
    //点击上一页
    pageLeft = () =>{
        var curr = this.state.pageCurr;
        if(curr > 1){curr--;}
        this.page(curr);
    }
    //点击下一页
    pageRight = () =>{
        var curr = this.state.pageCurr;
        if(curr < this.state.totalPage){curr++;}
        this.page(curr);
    }
    //点击某页码
    handlePage = (e) => {
        this.page(e.target.value);
    }
    render() {
        return (
            <div className="pages">
                <ul>
                    {this.state.pages}
                </ul>
            </div>
        )
    }
}
