import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import Pages from '../../components/Pages'

export default class All extends Component {
    constructor(){
        super();
        this.state = {
            page:1,
            data:[]
        }
    }
    handlePage = (e) => {
        this.setState({
            page:e.target.value
        })
    }
    componentDidMount(){
        fetch('https://cnodejs.org/api/v1/topics?tab=all')
            .then((res)=>{
                return res.json();
            }).then((res)=>{
                this.setState({
                    data:res.data
                });
                for(var i=0;i<this.state.data.length;i++){
                    var newdata = this.state.data;
                    newdata[i].newtitle = this.state.data[i].title.length>60?this.state.data[i].title.slice(0,60)+'...':this.state.data[i].title;
                    this.setState({
                        data:newdata
                    })
                }
            })
    }
    // shouldComponentUpdate(nextProps,nextState){
	// 	console.log(nextProps)		//父组件传给子组件的值，这里没有会显示空
	// 	console.log(nextState)		//数据更新后的值
	// 	return true;				//返回true，确认更新
	// }
    componentDidUpdate(prevProps,prevState){
        if(prevState.page !== this.state.page){
            fetch('https://cnodejs.org/api/v1/topics?tab=all&page='+this.state.page)
            .then((res)=>{
                return res.json();
            }).then((res)=>{
                this.setState({
                    data:res.data
                });
                for(var i=0;i<this.state.data.length;i++){
                    var newdata = this.state.data;
                    newdata[i].newtitle = this.state.data[i].title.length>60?this.state.data[i].title.slice(0,60)+'...':this.state.data[i].title;
                    this.setState({
                        data:newdata
                    })
                }
            })
        }
    }
    render() {
        return (
        <div className="list">
            <ul>
                {
                    this.state.data.map((item,index)=>(
                        <li key={index} className="li">
                            <img src={item.author.avatar_url} alt={item.author.loginname}/>
                            <div className="count">
                                <span className="reply">{item.reply_count}</span>/
                                <span className="visit">{item.visit_count}</span>
                            </div>
                            <Link className="title" to={'/topic/'+item.id}>{item.newtitle}</Link>
                            <div className="right">
                                <img src={item.author.avatar_url}/>
                                <span className="time">2小时前</span>
                            </div>
                        </li>
                    ))
                } 
            </ul>
            <Pages handlePage={this.handlePage.bind(this)}/>
        </div>
        )
    }
}
