import React, { Component } from 'react'

export default class Topics extends Component {
    constructor(){
        super();
        this.state = {
            data:[]
        }
    }
    componentDidMount(){
        let url = this.props.location.pathname;
        fetch('https://cnodejs.org/api/v1'+url)
            .then((res)=>{
                return res.json();
            }).then((res)=>{
                this.setState({
                    data:res.data
                });
            })
    }
    render() {
        return (<div dangerouslySetInnerHTML={{ __html:this.state.data.content}}></div>)
    }
}
