import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import Pages from '../../components/Pages'

export default class All extends Component {
    constructor(){
        super();
        this.state = {
            data:[]
        }
    }
    componentDidMount(){
        fetch('https://cnodejs.org/api/v1/topics?tab=share')
            .then((res)=>{
                return res.json();
            }).then((res)=>{
                this.setState({
                    data:res.data
                });
                for(var i=0;i<this.state.data.length;i++){
                    var newdata = this.state.data;
                    newdata[i].newtitle = this.state.data[i].title.length>48?this.state.data[i].title.slice(0,48)+'...':this.state.data[i].title;
                    this.setState({
                        data:newdata
                    })
                }
                console.log(this.state.data);
            })
        
    }
    render() {
        return (<div>
            <ul>
                {
                    this.state.data.map((item,index)=>(
                        <li key={index} className="li">
                            <img src={item.author.avatar_url} alt={item.author.loginname}/>
                            <div className="count">
                                <span className="reply">{item.reply_count}</span>/
                                <span className="visit">{item.visit_count}</span>
                            </div>
                            <Link className="title" to={'/topic/'+item.id}>{item.newtitle}</Link>
                            <div className="right">
                                <img src={item.author.avatar_url}/>
                                <span className="time">1小时前</span>
                            </div>
                        </li>
                    ))
                } 
            </ul>
            <Pages/>
        </div>)
    }
}
